<template>
  <div id="app">
    <div class="players">
      <div :class="{ active: currentPlayer === 'a' }" class="score left">{{ players.a.score }}</div>
      <div :class="{ active: currentPlayer === 'b' }" class="score">{{ players.b.score }}</div>
      <div class="legs left">
        <p>LEGS</p>
        <p>{{ players.a.legs }}</p>
      </div>
      <div class="legs">
        <p>LEGS</p>
        <p>{{ players.b.legs }}</p>
      </div>
    </div>
    <div class="current-score">
      <div v-for="(score, i) in currentScores" :key="i" @click="removeCurrentScore(i)">
        {{ score.base }} <span v-if="score.mult">{{ score.mult }}x</span>
      </div>
    </div>
    <div class="enter-score">
      <div class="entries">
        <div class="entry" v-for="(entry, i) in entering" :key="i">
          {{ entry }}
        </div>
      </div>
      <button @click="submit">Insert</button>
    </div>
    <div class="numbers">
      <div
        @click="enter(number)"
        v-for="number in numbers"
        :key="number">
        {{ number }}
      </div>
    </div>
    <div class="bull">
      <div @click="enter(25)" class="green"></div>
      <div @click="enter(50)" class="red"></div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    otherPlayer() {
      return this.currentPlayer === 'a' ? 'b' : 'a';
    },
    overThrown() {
      const score = this.currentScores.reduce((acc, curr) => acc + curr.base * curr.mult, 0);
      return this.players[this.currentPlayer].score - score <= 0 && !this.finished();
    }
  },
  data() {
    return {
      players: {
        a: {
          score: 50,
          legs: 0
        },
        b: {
          score: 501,
          legs: 0
        }
      },
      currentScores: [],
      entering: [],
      numbers: [],
      currentPlayer: 'a'
    }
  },
  mounted() {
    for(let i = 1; i <= 20; i++) {
      this.numbers.unshift(i);
    }
  },
  methods: {
    enter(number) {
      if((this.entering.includes(25) && number === 25) || (this.entering.includes(50) && number === 50)) {
        return;
      }
      if((this.entering.length === 0 || this.entering.includes(number)) && this.entering.length < 3) {
        this.entering.push(number);
      } else {
        this.entering = [number];
      }
    },
    submit() {
      this.currentScores.push({ base: this.entering[0] || 0, mult: this.entering.length || 1 });
      this.entering = [];

      if(this.overThrown) {
        this.currentScores = [];
        this.entering = [];
        return this.currentPlayer = this.otherPlayer;
      }
      if(this.finished()) return;
      if(this.currentScores.length === 3) {
        this.addScore();
      }
    },
    addScore() {
      const score = this.currentScores.reduce((acc, curr) => acc + curr.base * curr.mult, 0);
      const playerScore = this.players[this.currentPlayer].score - score;
      if(playerScore <= 0) {
        this.currentScores = [];
        return this.currentPlayer = this.otherPlayer;
      }

      this.players[this.currentPlayer].score -= score;
      this.currentPlayer = this.otherPlayer;
      this.currentScores = [];
    },
    removeCurrentScore(i) {
      this.currentScores.splice(i, 1);
    },
    finished() {
      if(this.currentScores.length === 0) return false;
      const double = this.currentScores[this.currentScores.length - 1].mult === 2;
      const special = this.currentScores[this.currentScores.length - 1].base === 50;
      const score = this.currentScores.reduce((acc, curr) => acc + curr.base * curr.mult, 0);
      if((double || special) && score === this.players[this.currentPlayer].score) {
        this.players[this.currentPlayer].legs += 1;
        this.resetGame();
        return true;
      }
      return false;
    },
    resetGame() {
      this.players.a.score = 501;
      this.players.b.score = 501;
      this.currentPlayer = this.otherPlayer;
      this.currentScores = [];
    }
  }
}
</script>

